import axios from 'axios';
import React, { useEffect, useState } from "react";
import './Coffee.scss';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Helmet} from "react-helmet";

export default function CoffeeApp() {
  const [data, setVal] = useState();

  const getAnswer = async () => {
    await axios.get("https://api-machine-learning-coffee.herokuapp.com/arabic_coffee")
      .then(resp => {
        setVal(resp.data)
      })
      .catch(err => {
        
      });
  };

  useEffect(() => {
    getAnswer();
  }, [data]);

  if (data) {
    return (
    <>
    <Helmet>
      <title>GRU Predict Coffee Arabic</title>
    </Helmet>
    <div className="container">
      <div className="row flex-items-xs-middle flex-items-xs-center box-gradient">
        <h2 className="title_text">
          Previsão do Indicador Café Arábica - Cepea/Esalq
        </h2>
        <div className="col-xs-12 col-lg-4">
          <div className="card text-xs-center">
            <div className="card-header text-center">
              <h4 className="card-title"> 
                {data.forecast.data_anterior}<br></br>
                <span className='message-warning'><br></br></span>
              </h4>
              <h2 className="display-2">
                R$ {data.last_line.valor_anterior}
              </h2>
            </div>
            <div className="card-block text-center">
              <ul className="list-group">
                <li className="list-group-item">Saca 60/kg</li>
              </ul>
            </div>
          </div>
        </div>
        <div className="col-xs-12 col-lg-4">
          <div className="card text-xs-center">
            <div className="card-header text-center">
              <h4 className="card-title"> 
                {data.forecast.data}<br></br>
                <span className='message-warning'><br></br></span>
              </h4>
              <h2 className="display-2">
                R$ {data.last_line.valor_café}
              </h2>
            </div>
            <div className="card-block text-center">
              <ul className="list-group">
                <li className="list-group-item">Saca 60/kg</li>
              </ul>
            </div>
          </div>
        </div>
        <div className="col-xs-12 col-lg-4">
          <div className="card text-xs-center">
            <div className="card-header text-center">
              <h4 className="card-title"> 
                {data.forecast.data_futura}<br></br>
                <span className='message-warning'>Predict by GRU</span>
              </h4>
              <h2 className="display-2">
                R$ {data.forecast.forecast}
              </h2>
            </div>
            <div className="card-block text-center">
              <ul className="list-group">
                <li className="list-group-item">Saca 60/kg</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <Helmet>
      <script src="https://unpkg.com/react/umd/react.production.min.js" crossorigin></script>
      <script
        src="https://unpkg.com/react-dom/umd/react-dom.production.min.js"
        crossorigin></script>
      <script
        src="https://unpkg.com/react-bootstrap@next/dist/react-bootstrap.min.js"
        crossorigin></script>
      </Helmet>
    </div>
    </>
    );
  } else {
    return (
      <>
      <Helmet>
        <title>CARREGANDO...</title>
      </Helmet>
      <div className='body'>
        <div className="wrap_">
          <div className="loading">
            <div className="bounceball_"></div>
            <div className="text_">CARREGANDO...</div>
          </div>
        </div>
      </div>
      </>
    );
  }
}